.list {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden auto;
    .list-table {
        width: 100%;
        height: 100%;
        padding: 15px;
    }
    .list-page {
        display: flex;
        margin-top: 20px;
        justify-content: flex-end;
    }
}